<template>
    <div>
        <article>
            <p></p>
            <p>OBWEB</p>
            <input type="text" name="invitationCd" placeholder="Invitation Code" v-model="invCode" class="topInput">

            <div class="buttonBase" v-on:click="toInput"></div>
        </article>
        <article style="margin-top:50px;">
            <p> <br></p>
            <input type="text" name="mail" placeholder="Email " v-model="req.mail" class="topInput">
            <input type="text" name="name" placeholder=" " v-model="req.name" class="reqInput">
            <input type="text" name="comment" placeholder="" v-model="req.comment" class="reqInput">
            <div class="buttonBase" v-on:click="toReq"></div>

        </article>
    </div>
</template>

<script>
export default {
    name : "InputInvitation",

    data(){
        return{
            invCode : "",
            req : {
                mail : "",
                name : "",
                comment : ""
            }
        }
    },

    created()
    {
        // URL
        let query = this.$route.query
        console.log(JSON.stringify(query))
        if(query != null && query.invCode != null)
        {
            this.invCode = query.invCode
        }
    },

    methods:{
        toInput(){
            this.$store.dispatch('setInvCode', this.invCode)
            this.$router.push({name:'reg-input'})
        },

        toReq()
        {
            this.$store.dispatch('reqInvCd', this.req)
        }
    }
}
</script>

<style scoped>
    .topInput{
        margin: 30px 0px 5px 0px;
    }
    .reqInput{
        margin:5px 0px;
    }
</style>


